<template>
    <div ref="timeBox">
        <h1 ref="timeNumber">{{ currentHour }}:{{ currentMinute }}:{{ currentSecond }}</h1>
        <h2 ref="timeDay">{{ currentYear }}-{{ currentMonth }}-{{ currentDate }} {{ currentDay }}</h2>
        <h2>Designed By KKKZOZ</h2>
        <button @click="changeTheme" id="changeButton"></button>
    </div>
</template>

<script lang='ts'>
//TODO: 对CSS样式进行进一步优化
//TODO: 直接用v-bind,直接对元素的属性绑定动态值
import { defineComponent } from 'vue'
import './logo.png'
export default defineComponent({
    data() {
        return {
            moon: "url('https://kkkzoz-1304409899.cos.ap-chengdu.myqcloud.com/img/moon.svg')",
            sun: "url('https://kkkzoz-1304409899.cos.ap-chengdu.myqcloud.com/img/sun.svg')",
            currentHour: '0',
            currentMinute: '0',
            currentSecond: '0',
            currentYear: '0',
            currentMonth: '0',
            currentDate: '0',
            currentDay: '0',
            currentTheme: {
                backgroundColor: 'black',
                fontColor: 'white',
            },
            weekUtility:['Monday','Tuesday','Wednesday', 'Thursday','Friday', 'Saturday','Sunday'],
            //TODO:以下4个属性改为null更为合适，但是会报错
            bodyElement: <HTMLElement>document.getElementById('body'),
            timeBox: <HTMLCanvasElement>this.$refs.timeBox,
            timeNumber: <HTMLCanvasElement>this.$refs.timeNumber,
            timeDay: <HTMLCanvasElement>this.$refs.timeDay,
            button: <HTMLElement>document.getElementById('changeButton'),


        }
    },
    methods: {
        changeTheme() {
            let darkMode = {
                backgroundColor: 'black',
                fontColor: 'white',
            };
            let dayMode = {
                backgroundColor: 'white',
                fontColor: 'black'
            }
            if (this.currentTheme.backgroundColor === darkMode.backgroundColor) {
                this.currentTheme = dayMode;
                this.button.style.backgroundImage = this.moon;
                return;
            }

            if (this.currentTheme.backgroundColor === dayMode.backgroundColor) {
                this.currentTheme = darkMode;
                this.button.style.backgroundImage = this.sun;
                return;
            }
        },
        updateTime() {
            let time = new Date();
            if (time.getHours() < 10) {
                this.currentHour = '0' + time.getHours();
            } else {
                this.currentHour = time.getHours().toString();
            }
            if (time.getMinutes() < 10) {
                this.currentMinute = '0' + time.getMinutes();
            } else {
                this.currentMinute = time.getMinutes().toString();
            }
            if (time.getSeconds() < 10) {
                this.currentSecond = '0' + time.getSeconds();
            } else {
                this.currentSecond = time.getSeconds().toString();
            }
            this.currentYear = time.getFullYear().toString();
            this.currentMonth = time.getMonth().toString();
            this.currentDate = time.getDate().toString();
            this.currentDay = this.weekUtility[time.getDay() - 1];
        }
    },
    watch: {
        currentTheme() {

            this.bodyElement.style.backgroundColor = this.currentTheme.backgroundColor;
            this.timeBox.style.backgroundColor = this.currentTheme.backgroundColor;
            this.timeNumber.style.color = this.currentTheme.fontColor;
            this.timeDay.style.color = this.currentTheme.fontColor;

        }
    },
    mounted() {
        this.bodyElement = <HTMLElement>document.getElementById('body');
        this.timeBox = <HTMLCanvasElement>this.$refs.timeBox;
        this.timeNumber = <HTMLCanvasElement>this.$refs.timeNumber;
        this.timeDay = <HTMLCanvasElement>this.$refs.timeDay;
        this.button = <HTMLElement>document.getElementById('changeButton');

        this.updateTime();
        this.bodyElement.style.backgroundColor = this.currentTheme.backgroundColor;
        this.timeBox.style.backgroundColor = this.currentTheme.backgroundColor;
        this.timeNumber.style.color = this.currentTheme.fontColor;
        this.timeDay.style.color = this.currentTheme.fontColor;
        this.button.style.backgroundImage = this.sun;

        setInterval(() => {
            this.updateTime();


        }, 1000);
    }
})
</script>

<style lang="less">
h1 {
    color: white;
    font-family: "Oswald", sans-serif, monospace;
    font-size: 250px;
    font-weight: 700;
}
h2{
    font-family: "JetBrains Mono", sans-serif, monospace;
    font-size: 50px;
    font-weight: 700;
}
p {
    color: white;
}
#changeButton {
    background-size: 100px 100px;
    background-color: transparent;
    height: 100px;
    width: 100px;
    border: none;
}
</style>